body {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    color: #333;
}

header {
    background-color: #004b8d; /* Example club color */
    color: white;
    padding: 10px;
    text-align: center;
}

h1, h2 {
    font-family: 'Open Sans', sans-serif; /* Match with club's typography */
}

section {
    margin: 20px;
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

button {
    background-color: #004b8d;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #003a6b;
}

footer {
    text-align: center;
    padding: 10px;
    background-color: #f7f7f7;
    margin-top: 20px;
    font-size: 0.9em;
}

a {
    color: #004b8d;
    text-decoration: none;
}
